<template>
  <v-app app :dark="goDark" color="gray">
    <div>
      <v-app-bar color="blue-grey darken-4" dark>
        <v-toolbar-title>ACA Dashboard for {{acctName}}</v-toolbar-title>
        <v-spacer></v-spacer>
      </v-app-bar>
    </div>
    <div>
      <v-container>
        <acaquicktable
          v-for="(item, index) in reports"
          :key="index"
          :reports="item.details"
          :reporthdr="item.report_group"
        />
      </v-container>
    </div>
    <v-dialog v-model="dialog" persistent max-width="290">
      <v-card>
        <v-card-title class="headline">Sample Data</v-card-title>
        <v-card-text>The data presented in this application is for demo purposes only.</v-card-text>
        <v-card-actions>
          <v-spacer></v-spacer>
          <v-btn color="green darken-1" text @click="dialog = false">I Understand</v-btn>
        </v-card-actions>
      </v-card>
    </v-dialog>
  </v-app>
</template>
<style scoped>
.theme--light.v-application {
  background: #f8f8f8;
}
</style>
<script>
import reports from "@/store/reports.js";
import UIkit from "uikit";
import Icons from "uikit/dist/js/uikit-icons";
import acaquicktable from "@/components/acaquicktable.vue";
import $ from "jquery";
UIkit.use(Icons);
export default {
  name: "App",

  components: {
    acaquicktable
  },
  watch: {
    goDark: function() {
      var self = this;
      if (self.goDark) {
        this.$vuetify.theme.dark = true;
        this.selTheme = "Dark";
      } else {
        this.$vuetify.theme.dark = false;
        this.selTheme = "Light";
      }
    }
  },
  data: () => ({
    drawer: true,
    goDark: false,
    acctName: "Test Account",
    selTheme: "Light",
    dialog: false,
    reports: reports.reports
  }),
  methods: {
    getData: function() {
      $.ajax({
        type: "POST",
        url: "/web_projects/MyEnrollWebService/ACAWebMethod.aspx/GetACAReport",
        data: JSON.stringify({
          report_id: 1
        }),
        contentType: "application/json; charset=utf-8"
      })
        .done(function(n) {
          this.reports.push(JSON.parse(n.d)[0]);
        })
        .fail(function() {
          console.log("fail");
        });
      $.ajax({
        type: "POST",
        url: "/web_projects/MyEnrollWebService/ACAWebMethod.aspx/GetACAReport",
        data: JSON.stringify({
          report_id: 2
        }),
        contentType: "application/json; charset=utf-8"
      })
        .done(function(n) {
          this.reports.push(JSON.parse(n.d)[0]);
        })
        .fail(function() {
          console.log("fail");
        });
      $.ajax({
        type: "POST",
        url: "/web_projects/MyEnrollWebService/ACAWebMethod.aspx/GetACAReport",
        data: JSON.stringify({
          report_id: 3
        }),
        contentType: "application/json; charset=utf-8"
      })
        .done(function(n) {
          this.reports.push(JSON.parse(n.d)[0]);
        })
        .fail(function() {
          console.log("fail");
        });
      $.ajax({
        type: "POST",
        url: "/web_projects/MyEnrollWebService/ACAWebMethod.aspx/GetACAReport",
        data: JSON.stringify({
          report_id: 4
        }),
        contentType: "application/json; charset=utf-8"
      })
        .done(function(n) {
          this.reports.push(JSON.parse(n.d)[0]);
        })
        .fail(function() {
          console.log("fail");
        });
      $.ajax({
        type: "POST",
        url: "/web_projects/MyEnrollWebService/ACAWebMethod.aspx/GetACAReport",
        data: JSON.stringify({
          report_id: 5
        }),
        contentType: "application/json; charset=utf-8"
      })
        .done(function(n) {
          this.reports.push(JSON.parse(n.d)[0]);
        })
        .fail(function() {
          console.log("fail");
        });
      $.ajax({
        type: "POST",
        url: "/web_projects/MyEnrollWebService/ACAWebMethod.aspx/GetACAReport",
        data: JSON.stringify({
          report_id: 6
        }),
        contentType: "application/json; charset=utf-8"
      })
        .done(function(n) {
          this.reports.push(JSON.parse(n.d)[0]);
        })
        .fail(function() {
          console.log("fail");
        });
    }
  },
  created: function() {
    var self = this;
    if (
      window.location.href.toLowerCase().indexOf("bastest") > -1 ||
      window.location.href.toLowerCase().indexOf("myenroll") > -1
    ) {
      self.reports = [];
      self.getData();
    } else {
      self.dialog = true;
    }
  }
};
</script>
